<template>
  <div>
    <div class="row">
      <date-select :index="PHOTO_GALLERIES"
                   :on-change="selectDate"></date-select>
    </div>
    <div v-for="(list, date) in galleries" class="row animated fadeIn slow">
      <transition name="fade">
        <!--<event-list v-if="selected === date" :date="date" :events="list">-->
        <!--</event-list>-->
      </transition>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import DateSelect from '../DateSelect'

  import PhotoGalleriesBean from './PhotoGalleriesBean'

  const photoGalleriesBean = new PhotoGalleriesBean();

  export default {
    data() {
      return {
        selected: '',
        galleries: {}
      }
    },
    methods: {
      selectDate(date) {
        this.selected = date;
        photoGalleriesBean.fetch(date).then(galleries => {
          Vue.set(this.galleries, date, galleries);
        })
      }
    },
    components: {
      DateSelect
    }
  }
</script>
